/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-smart-list-block {
	.smart-list {
		padding: 0;
		margin: 0;
		list-style: none;
		display: flex;
		gap: 24px;

		.smart-item {
			display: flex;
			align-items: center;
			gap: 12px;
		}

		&.is-horizontal {
			flex-direction: row;
		}

		&.is-vertical {
			flex-direction: column;
		}

		&.alignment-left {
			justify-content: flex-start;
			align-items: flex-start;
		}

		&.alignment-center {
			justify-content: center;
			align-items: center;
		}
		&.alignment-right {
			justify-content: flex-end;
			align-items: flex-end;
		}
	}
}

.components-panel__body {
	.tabs {
		display: flex;
		align-items: center;
		padding: 16px 16px 16px 0px;
		width: 248px;
		border: none;
		height: 100%;

		&.tabs--only-text,
		&.tabs--alignment {
			border: 1px solid #dddddd;
			border-radius: 2px;
			padding: 6px;
		}

		.tab {
			cursor: pointer;
			padding: 6px 6px 12px;
			display: flex;
			gap: 8px;
			justify-content: center;
			align-items: center;
			width: 100%;
			height: 100%;
			.tab-title {
				font-size: 14px;
				line-height: 20px;
				font-weight: 500;
			}

			&.tab--underline {
				border-bottom: 3px solid #979797;

				.tab-icon svg {
					display: block;
					fill: currentColor;
				}

				&.active {
					border-bottom: 3px solid #641dd7;
					color: #6f22dd;
				}
			}
			&.tab--icon {
				padding: 0;
				border: none;
				.tab-icon svg rect:first-child {
					stroke: currentColor;
				}
				.tab-title {
					display: none;
				}
				&.active {
					color: #1a74e4;
				}
			}
			&.tab--only-text {
				padding: 0;
				border: none;
				.tab-icon {
					display: none;
				}
				&.active {
					padding: 5px;
					border-radius: 2px;
					border: 1px solid var(--Primary-2-100, #d6c2f5);
					background: var(--Primary-2-50, #efe6fb);
					color: var(--Primary-2-500, #6f22dd);
					font-size: 13px;
					font-style: normal;
					font-weight: 400;
					line-height: 20px;
				}
			}
			&.tab--alignment {
				display: flex;
				padding: 2px 6px;
				justify-content: center;
				align-items: center;
				.tab-title {
					display: none;
				}
				&.active {
					border-radius: 2px;
					border: 1px solid var(--Primary-2-100, #d6c2f5);
					background: var(--Primary-2-50, #efe6fb);
					color: var(--Gutenberg-White, #fff);
					font-size: 13px;
					font-style: normal;
					font-weight: 400;
					line-height: 20px;
				}
			}
		}
		.tab:hover {
			color: #641dd7;
		}
	}
	.range-control {
		display: flex;
		padding: 16px 0px 0px 0px;
		align-items: flex-start;
		.content {
			display: flex;
			gap: 16px;

			p {
				color: var(--Gutenberg-Gray-800, #2f2f2f);
				font-size: 13px;
				font-style: normal;
				font-weight: 400;
				line-height: 20px;
			}

			.range-measure {
				div {
					display: flex;
					justify-content: flex-end;
					align-items: center;
					gap: 8px;
					flex: 1 0 0;
				}
				.pixel {
					display: flex;
					width: 23px;
					height: 20px;
					min-width: 23px;
					padding: 8px 5px;
					justify-content: center;
					align-items: center;
					gap: 8px;
					border-radius: 2px;
					border: 1px solid var(--Grey-Grey-30, #8c8f94);
					background: var(--White, #fff);

					p {
						color: var(--Grey-Grey-30, #8c8f94);
						text-align: center;
						font-size: 10px;
						font-style: normal;
						font-weight: 400;
						line-height: normal;
					}
				}
			}

			.desktop {
				display: flex;
				width: 22px;
				height: 20px;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				gap: 8px;
				border-radius: 2px;
				border: 1px solid var(--Grey-Grey-30, #8c8f94);
				background: var(--White, #fff);
			}
		}
	}
}

.input-range {
	--wp-components-color-accent: #f97316;
}
